<template>
    <div class="content">
        <div class="container-fluid">
            <div class="row">
                <div class="col-8">
                    <h4 class="mb-4 mt-2">{{$t('common.comments.labels.list')}}</h4>
                </div>
            </div>
            <div class="row">
                <div class="col-12">
                    <div class="row">
                        <div class="col-12">
                            <card>
                                <datatable :class="{'loading-table': loading}" v-if="datatable.data" v-bind="datatable" />
                            </card>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <modal
                name="comment-modal"
                height="auto"
                :draggable="true"
                :classes="['v--modal', 'm-modal']">
            <div class="modal-body">
                <div class="container-fluid">
                    <div class="row">
                        <div class="col">
                            <i class="fas fa-times fa-2x btn-modal-close text-success" slot="top-right" @click="$modal.hide('comment-modal')"></i>
                            <h4 class="mt-0">{{$t('common.comments.labels.new')}}</h4>
                            <hr>
                            <form @submit.prevent="$emit('submitAction', item)">
                                <div class="form-group row">
                                    <label class="col-4 col-form-label">{{$t('common.comments.input.comment')}} :</label>
                                    <div class="col-8">
                                        <input type="text" class="form-control" v-model="comment.id">
                                    </div>
                                </div>
                                <div class="form-group row">
                                    <label class="col-4 col-form-label">{{$t('common.comment.input.body')}} :</label>
                                    <div class="col-8">
                                        <input type="text" class="form-control" v-model="comment.body">
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-simple btn-sm px-3" @click="$modal.hide('comment-modal')">{{$t('common.close')}}</button>
                <button type="button" class="btn btn-primary btn-sm btn-round btn-fill px-4" @click="saveComment">{{$t('common.buttons.save')}}</button>
            </div>
        </modal>
    </div>
</template>
<script>

  import Vue from 'vue'
  import Card from 'src/components/UIComponents/Cards/Card.vue'
  import TableCustomColumn from 'src/components/UIComponents/DataTable/TableCustomColumn';
  import TableActions from 'src/components/UIComponents/DataTable/TableActions';
  import ApprovalActions from 'src/components/UIComponents/DataTable/ApprovalActions';
  import {mapState, mapGetters} from 'vuex'
  import {Comment} from "src/models/definition";

  export default {
    components: {
      Card,
      TableActions,
      TableCustomColumn
    },
    data () {
      return {
        loading: false,
        selectedBanner: null,
        comment : new Comment(),
        mode : 'save',
        datatable: {
          columns: [
            { title: 'ID', field: 'id', sortable: true},
            { title:  this.$t('common.comments.datatable.user'),   field: 'user.email', tdComp: TableCustomColumn},
            { title:  this.$t('common.comments.datatable.comment'),  field: 'body', tdComp: TableCustomColumn},
            { tdComp: ApprovalActions, visible: 'true', thStyle: { width: '10%' }, tdStyle: { width: '10%' } },
            { tdComp: TableActions, visible: 'true', thStyle: { width: '10%' }, tdStyle: { width: '10%' } }
          ],
          data: [],
          total: 0,
          query: {sort: 'id', order: 'asc'},
          selection: this.selection ? [] : null,
          HeaderSettings: false,
          supportNested: true,
          supportBackup: true,
          xprops: {
            eventbus: new Vue()
          }
        },
        bus: new Vue()
      }
    },
    watch: {
      "datatable.query": {
        handler (query) {
          this.getAllComments()
        },
        deep: true
      }
    },
    computed: {
      ...mapState('definition', {
        comments: state => state.comments
      })
    },
    created(){
      this.datatable.xprops.eventbus.$on('edit', item => {
        this.edit(item)
      });
      this.datatable.xprops.eventbus.$on('delete', item => {
        this.delete(item)
      });
      this.datatable.xprops.eventbus.$on('changeStatus', item => {
        this.changeStatus(item)
      });
    },
    methods:{
      getAllComments(){
        let query = this.datatable.query;
        if(query.sort == 'name') {
          query.sort = `${query.sort}.${this.$store.getters['account/getActiveLanguage'].code}`
        }
        let filter = {
          sort:  query.sort,
          order: query.order,
          limit: query.limit,
          offset:query.offset
        };
        this.loading = true;
        this.$store.dispatch('definition/getAllComments', {filter: filter}).then((response) => {
          this.loading = false;
          this.datatable.total = response.pagination && response.pagination.recordCount ? response.pagination.recordCount : 1;
          this.datatable.data = response.set;
        })
      },
      edit(item){
        this.mode = 'edit';
        this.comment = item;
        this.$modal.show('comment-modal');
      },
      delete(item) {
        this.$modal.show('dialog', {
          title: 'Warning!!',
          text: `${item.default} will be deleted. Are you sure?`,
          buttons: [
            {
              title: 'Cancel'
            },
            {
              title: 'Yes',
              class: 'btn btn-danger btn-fill',
              handler: () => {
                this.$store.dispatch('definition/deleteComment', {data: item}).then(() => {
                  this.notify('Comments Deleted.', 'success');
                  this.$modal.hide('dialog');
                  this.getAllComments();
                })
              }
            }
          ]
        });
      },
      changeStatus(item) {
            this.$store.dispatch('definition/changeCommentStatus', {data: item}).then(() => {
                this.notify('Comment status has been changed', 'success');
            })
      },
      saveComment(){
        if(this.mode === 'save') {
          this.$store.dispatch('definition/createComment', {data: this.comment}).then((response) => {
            this.getAllComments();
            this.$modal.hide('comment-modal');
            this.notify(this.$t(response.message), 'success');
          }).catch(error =>{
            this.notify(this.$t(response.message), 'err');
          });
        }else{
          this.$store.dispatch('definition/updateKeyword', {data: this.comment}).then((response) => {
              this.getAllComments();
              this.$modal.hide('comment-modal');
              this.notify(this.$t(response.message), 'success');
          }).catch(error =>{
            this.notify(this.$t(response.message), 'err');
          });
        }
      },
    }
  }
</script>
<style>
</style>



